.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #09d3ac;
}


#date {
    text-align: center;
}



.stripe {
    border: 1px solid lightgray;
    padding: 40px;
    background: white;
    border-radius: 15px;
    padding-bottom: 80px;
}

.stripe__data {
    max-width: 300px;
    margin: auto;
}

.stripe__type,
.stripe__amount {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
}

.stripe input[type="radio"] {
    display: none;
}

.stripe input[type="radio"]+label {
    cursor: pointer;
    padding: 5px 20px;
    border: 1px solid #6e6262;
    border-radius: 5px;
}

.stripe input[type="radio"]:checked+label {
    border: 2px solid black;
}

.stripe .custom-price-button label {
    margin-bottom: 10px;
    width: 100%;
    display: block;
    box-sizing: border-box;
}

.stripe .custom-price-input {
    margin-bottom: 20px;
}

.stripe .custom-price-input.closed {
    height: 0px;
    overflow: hidden;
    transition: 0.3s;
}

.stripe .custom-price-input.active {
    height: auto;
    transition: 0.3s;
}

.stripe .custom-price-input input:disabled {

    visibility: hidden;
    opacity: 0;
    transition: 0.3s;
}

.stripe .custom-price-input input,
.stripe .payment-description input {

    width: 100%;
    box-sizing: border-box;

    border: 1px solid lightGrey;
    margin-bottom: 7px;
    margin-top: 5px;
    min-height: 30px;
    padding-left: 13px;
    height: 40px;
    border-radius: 0px;
    font-family: "PT Sans",
        sans-serif;
    font-size: 18px;


    height: auto;
    visibility: visible;
    opacity: 1;
    transition: 0.3s;
}

.stripe .payment-description {
    margin-bottom: 20px;
}